body {
    min-width: 900px;
    height: 100vh;
    padding-top: 20px;
}

%text {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    font-weight: 700;
    color: #fff;
}
%ul-unstyle {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
%btn {
    border-radius: 10px;
    border: none;
    background: #5FF954;
    background: -webkit-linear-gradient(top, #5FF954, #2B7118);
    background: -moz-linear-gradient(top, #5FF954, #2B7118);
    background: linear-gradient(to bottom, #5FF954, #2B7118);
    padding: 10px 20px;
    box-shadow: 2px 2px 2px #000;
    @extend %text;
    text-transform: uppercase;
    &:disabled {
        background: rgb(127, 134, 126);
        background: -webkit-linear-gradient(top,rgb(110, 116, 109), rgb(48, 49, 48));
        background: -moz-linear-gradient(top, rgb(110, 116, 109), rgb(48, 49, 48));
        background: linear-gradient(to bottom, rgb(110, 116, 109), rgb(48, 49, 48));  
    }
}
%popup {
    position: absolute;
    top: 13%;
    left: 50%;
    transform: translateX(-50%);
    width: 480px;
    height: 250px;
    border-radius: 10px;
    background-image: url(../../dist/img/las-vegas.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 10px 5px 2px rgba($color: #000000, $alpha: 0.5);
}


