.show {
    display: flex !important;
}
.hidden {
    display: none !important;
}

@keyframes deal-main-user {
    0% {
        left: 44%;
        bottom: 230px;
        transform: rotate(0deg);
    }
    100% {
        left: 44%;
     bottom: 0px;
     transform: rotate(720deg);
    }
}
@keyframes deal-gamer-left-top {
    0% {
        bottom: 33%; 
        left: 44%;
        transform: rotate(0deg);
    }
    100% {
        bottom: 60%; 
     left: 0%;
     transform: rotate(720deg);
    }
}
@keyframes deal-gamer-right-top {
    0% {
        bottom: 30%;
        left: 44%;
        transform: rotate(0deg);
    }
    100% {
     bottom: 60%;   
     left: 88%;
     transform: rotate(720deg);
    }
}
@keyframes deal-gamer-right-bottom {
    0% {
        bottom: 230px;
        left: 44%;
        transform: rotate(0deg);
    }
    100% {
        bottom: 0px;  
     left: 88%;
     transform: rotate(720deg);
    }
}
@keyframes deal-gamer-left-bottom {
    0% {
        left: 44%;
        bottom: 230px;
        transform: rotate(0deg);
    }
    100% {
     left: 0%;  
     bottom: 0px;
     transform: rotate(720deg);
    }
}
@keyframes open-card {
    0% {
        transform: rotateY(0deg);
    }
    100% {
     transform: rotateY(90deg);
    }
}
.animation {
    animation-name: deal-main-user;
    animation-duration: 1s;
}
.animation-l-t {
    animation-name: deal-gamer-left-top;
    animation-duration: 1s;
}
.animation-r-t {
    animation-name: deal-gamer-right-top;
    animation-duration: 1s;
    z-index: 3;
}
.animation-l-b {
    animation-name: deal-gamer-left-bottom;
    animation-duration: 1s;
    z-index: 1;
}
.animation-r-b {
    animation-name: deal-gamer-right-bottom;
    animation-duration: 1s;
}
.rotate {
    animation-name: open-card;
    animation-duration: 0.5s;
}